import { useState } from 'react';
import PropTypes from 'prop-types';

// material-ui
import {
    Typography,
    Autocomplete,
    Button,
    InputAdornment,
    MenuItem,
    TextField,
    Divider,
    Tooltip,
    Accordion,
    AccordionDetails,
    AccordionSummary
} from '@mui/material';
import {
    Add as AddIcon,
    Check as CheckIcon,
    Cancel as CancelIcon,
    DeleteOutline as DeleteIcon,
    ExpandMore as ExpandMoreIcon
} from '@mui/icons-material';

let customNameArr = ['custom_info']; // 自定义字段名
let customKeyArr = []; // 自定义字段可选的key
function ProductAttributes({
    langPack,
    expanded,
    setExpanded,
    sourcing_trade,
    subject,
    keywords,
    finalCustomArr,
    finalAttributeArr, //使用中的属性
    allAttributeArr, //所有可选属性
    onAttributeAdd,
    onAttributeModified,
    onAttributeClear
}) {
    let [addAttribute, setAddAttribute] = useState(false); //需要添加属性吗
    let [attributeSelected, setAttributeSelected] = useState(''); //当前选择的属性
    let [attributeOptionArr, setAttributeOptionArr] = useState([]); //属性值列表（若有）
    let [customDisplay, setCustomDisplay] = useState(false); //是否显示
    let [customTip, setCustomTip] = useState(''); //定制信息值
    let [customValue, setCustomValue] = useState(''); //定制信息值
    let [customKey, setCustomKey] = useState(''); //定制信息
    let [customName, setCustomName] = useState(''); //定制信息

    const handleAccordionChange = (panel) => (event, isExpanded) => {
        setExpanded((prev) => {
            return { ...prev, [panel]: isExpanded };
        });
    };
    const onAttributeSelection = (e) => {
        let attributeObj = allAttributeArr.find((obj) => obj.attr_id === e.target.value);
        if (!attributeObj) return console.log('属性不属于该类目？');
        setAttributeSelected(attributeObj);
        let optionArr = attributeObj.attribute_values.attribute_value;
        if (optionArr) setAttributeOptionArr(optionArr);
        else setAttributeOptionArr([]);
    }; //选择属性

    const onAttributeOptionSelection = (e) => {
        let optionObj = attributeOptionArr.find((obj) => obj.attr_value_id === e.target.value);
        if (!optionObj) return console.log('选项不是该属性的选项？');
        setAttributeSelected((prev) => {
            return Object.assign({}, prev, { attr_value: optionObj });
        });
    }; //选择属性的可选项，若有，然后暂存

    const onCustomNameChange = (value) => {
        let tip = '';
        if (value === 'custom_info') {
            customKeyArr = ['Customized logo', 'Customized packaging', 'Graphic customization', 'Material'];
            tip = langPack.table.min_order_quantity;
        } else if (value === 'extra_context') {
            customKeyArr = [];
        } else {
            customKeyArr = [];
        }
        setCustomName(value);
        setCustomKey(''); //清理
        setCustomValue(''); //清理
        setCustomTip(tip);
    };
    const onCustomKeyChange = (value) => {
        setCustomKey(value); //清理
        setCustomValue(''); //清理
    };
    const onCustomAdd = () => {
        if (customName === 'custom_info') {
            if (customKey.trim() === '' || customValue.trim() === '') return queryResult(false, langPack.form.attributeMissing);
            if (isNaN(parseInt(customValue))) return queryResult(false, langPack.steps.numberOnly);
            onAttributeModified('custom_info', {
                custom_type: customKey,
                min_order_quantity: parseInt(customValue)
            });
        } else if (customName === 'extra_context') {
            return;
        }
        return;
    };
    return (
        <>
            <div>
                <Accordion expanded={expanded['panel1']} onChange={handleAccordionChange('panel1')}>
                    <AccordionSummary expandIcon={<ExpandMoreIcon />} aria-controls="panel1bh-content" id="panel1bh-header">
                        <Typography variant="h2" sx={{ width: '33%', flexShrink: 0, color: 'orangered', display: 'inline' }}>
                            {langPack.form.KeyInformation}
                        </Typography>
                        <Typography sx={{ color: 'text.secondary' }}>{langPack.steps.panel1}</Typography>
                    </AccordionSummary>
                    <AccordionDetails>
                        <Tooltip title={langPack.form.subject}>
                            <TextField
                                margin="normal"
                                fullWidth
                                key={subject}
                                id="subject"
                                variant="outlined"
                                label={<Typography variant="h3">{langPack.table.subject}</Typography>}
                                defaultValue={subject}
                                onChange={(e) => onAttributeModified('subject', e.target.value)}
                            />
                        </Tooltip>
                        <Tooltip title={langPack.form.keywords}>
                            <TextField
                                margin="normal"
                                fullWidth
                                key={keywords}
                                id="keywords"
                                variant="outlined"
                                label={<Typography variant="h3">{langPack.table.keywords}</Typography>}
                                defaultValue={keywords ? String(keywords.string) : ''}
                                onChange={(e) => onAttributeModified('keywords', e.target.value)}
                            />
                        </Tooltip>
                        <Tooltip title={langPack.form.price}>
                            <TextField
                                margin="normal"
                                key={sourcing_trade}
                                id="price"
                                variant="outlined"
                                label={<Typography variant="h3">{langPack.table.price}</Typography>}
                                InputProps={{
                                    startAdornment: (
                                        <InputAdornment position="start">
                                            {sourcing_trade ? sourcing_trade.fob_currency : 'USD'}
                                        </InputAdornment>
                                    )
                                }}
                                defaultValue={
                                    sourcing_trade
                                        ? sourcing_trade.fob_max_price === sourcing_trade.fob_min_price
                                            ? sourcing_trade.fob_min_price
                                            : `${sourcing_trade.fob_min_price} ~ ${sourcing_trade.fob_max_price}`
                                        : ''
                                }
                                onChange={(e) => {
                                    let v = e.target.value;
                                    if (v.trim() === '')
                                        return onAttributeModified('sourcing_trade', {
                                            fob_min_price: ''
                                        });
                                    v = v.trim().replace(/\s/g, '');
                                    let splitter = /~|-/g.test(v) ? '~' : '-';
                                    let prices = v.split(splitter);
                                    let fob_min_price = prices[0];
                                    let fob_max_price = prices[1];
                                    if (Number(fob_min_price) >= Number(fob_max_price)) fob_max_price = '';
                                    let { fob_currency, fob_unit_type } = sourcing_trade;
                                    onAttributeModified('sourcing_trade', { fob_min_price, fob_max_price, fob_currency, fob_unit_type });
                                }}
                            />
                        </Tooltip>
                        <Tooltip title={langPack.form.min_order_quantity}>
                            <TextField
                                margin="normal"
                                key={sourcing_trade}
                                id="min_order_quantity"
                                variant="outlined"
                                label={<Typography variant="h3">{langPack.table.min_order_quantity}</Typography>}
                                defaultValue={sourcing_trade && sourcing_trade.min_order_quantity ? sourcing_trade.min_order_quantity : ''}
                                InputProps={{
                                    endAdornment: (
                                        <InputAdornment position="end">
                                            {sourcing_trade ? sourcing_trade.min_order_unit_type : langPack.form.pieces}
                                        </InputAdornment>
                                    )
                                }}
                                onChange={(e) => {
                                    let { min_order_unit_type } = sourcing_trade;
                                    let min_order_quantity = e.target.value || '1';
                                    onAttributeModified('sourcing_trade', { min_order_quantity, min_order_unit_type });
                                }}
                            />
                        </Tooltip>
                        <Tooltip title={langPack.form.payment_methods}>
                            <TextField
                                margin="normal"
                                key={sourcing_trade}
                                id="payment_methods"
                                variant="outlined"
                                label={<Typography variant="h3">{langPack.table.payment_methods}</Typography>}
                                defaultValue={
                                    sourcing_trade && sourcing_trade.payment_methods ? String(sourcing_trade.payment_methods.string) : ''
                                }
                                onChange={(e) => {
                                    let payment_methods = e.target.value;
                                    if (payment_methods.trim() === '') return queryResult(false, langPack.form.attributeMissing);
                                    payment_methods = payment_methods.split(',');
                                    onAttributeModified('sourcing_trade', { payment_methods });
                                }}
                            />
                        </Tooltip>
                    </AccordionDetails>
                </Accordion>
                <Accordion expanded={expanded['panel2']} onChange={handleAccordionChange('panel2')}>
                    <AccordionSummary expandIcon={<ExpandMoreIcon />} aria-controls="panel2bh-content" id="panel2bh-header">
                        <Typography variant="h2" sx={{ width: '33%', flexShrink: 0, color: 'darkorange', display: 'inline' }}>
                            {langPack.form.ProductAttributes}
                        </Typography>
                        <Typography sx={{ color: 'text.secondary' }}>{langPack.steps.panel2}</Typography>
                    </AccordionSummary>
                    <AccordionDetails>
                        <>
                            <div style={{ paddingTop: '10px', paddingBottom: '20px' }}>
                                {addAttribute ? (
                                    <>
                                        {allAttributeArr.length ? (
                                            <>
                                                <TextField
                                                    id="add-attribute"
                                                    select
                                                    sx={{
                                                        minWidth: '120px',
                                                        padding: '5px',
                                                        '& .MuiOutlinedInput-root': { height: '29px', padding: '1px' }
                                                    }}
                                                    value={attributeSelected && attributeSelected.attr_id ? attributeSelected.attr_id : ''}
                                                    onChange={onAttributeSelection}
                                                >
                                                    <MenuItem key="" value="" disabled />
                                                    {allAttributeArr.map((option) => (
                                                        <MenuItem key={option.attr_id} value={option.attr_id}>
                                                            {option.en_name}
                                                        </MenuItem>
                                                    ))}
                                                </TextField>
                                                {attributeOptionArr.length ? (
                                                    <TextField
                                                        id="add-attribute-if-single-select"
                                                        select
                                                        value={
                                                            attributeSelected.attr_value && attributeSelected.attr_value.attr_value_id
                                                                ? attributeSelected.attr_value.attr_value_id
                                                                : ''
                                                        }
                                                        onChange={onAttributeOptionSelection}
                                                        variant="standard"
                                                    >
                                                        {attributeOptionArr.map((option) => (
                                                            <MenuItem key={String(option.attr_value_id)} value={option.attr_value_id}>
                                                                {option.en_name}
                                                            </MenuItem>
                                                        ))}
                                                    </TextField>
                                                ) : (
                                                    <></>
                                                )}
                                                <Button startIcon={<CheckIcon />} onClick={() => onAttributeAdd(attributeSelected)}>
                                                    {langPack.common.yes}
                                                </Button>
                                                <Button startIcon={<CancelIcon />} onClick={() => setAddAttribute((prev) => !prev)}>
                                                    {langPack.common.no}
                                                </Button>
                                            </>
                                        ) : (
                                            <Typography variant="body1">{langPack.form.attributeMissing}</Typography>
                                        )}
                                    </>
                                ) : (
                                    <>
                                        <Tooltip title={langPack.steps.addAttributes}>
                                            <Button variant="standard" onClick={() => setAddAttribute((prev) => !prev)}>
                                                <AddIcon />
                                            </Button>
                                        </Tooltip>
                                        <Tooltip title={langPack.steps.removeAttributes}>
                                            <Button variant="standard" onClick={onAttributeClear}>
                                                <DeleteIcon />
                                            </Button>
                                        </Tooltip>
                                    </>
                                )}
                            </div>
                            <Divider />
                            <br />
                            {finalAttributeArr.length &&
                                finalAttributeArr.map((obj) => (
                                    <Tooltip title={langPack.form.attributes} key={obj.value_name + obj.attribute_id}>
                                        <TextField
                                            margin="normal"
                                            id={String(obj.attribute_name + obj.value_name)}
                                            variant="outlined"
                                            label={<Typography variant="h3">{obj.attribute_name}</Typography>}
                                            defaultValue={obj.value_name}
                                            onBlur={(e) => {
                                                if (e.target.value === obj.value_name) return;
                                                onAttributeModified('attributes', {
                                                    attribute_id: obj.attribute_id,
                                                    attribute_name: obj.attribute_name,
                                                    //value_id: obj.value_id,
                                                    value_name: e.target.value
                                                });
                                            }}
                                        />
                                    </Tooltip>
                                ))}
                        </>
                    </AccordionDetails>
                </Accordion>
                <Accordion expanded={expanded['panel3']} onChange={handleAccordionChange('panel3')}>
                    <AccordionSummary expandIcon={<ExpandMoreIcon />} aria-controls="panel3bh-content" id="panel3bh-header">
                        <Typography variant="h2" sx={{ width: '33%', flexShrink: 0, color: 'coral', display: 'inline' }}>
                            {langPack.form.Advanced}
                        </Typography>
                        <Typography sx={{ color: 'text.secondary' }}>{langPack.steps.panel3}</Typography>
                    </AccordionSummary>
                    <AccordionDetails>
                        <>
                            {!customDisplay ? (
                                <>
                                    <Tooltip title={langPack.steps.addCustomInfo}>
                                        <Button variant="standard" onClick={() => setCustomDisplay((prev) => !prev)}>
                                            <AddIcon />
                                        </Button>
                                    </Tooltip>
                                </>
                            ) : (
                                <>
                                    <Autocomplete
                                        id="add-custom"
                                        sx={{
                                            display: 'inline-flex',
                                            minWidth: '180px',
                                            padding: '0px',
                                            '& .MuiOutlinedInput-root': { height: '29px', padding: '1px' }
                                        }}
                                        onChange={(e, value) => onCustomNameChange(value)}
                                        onBlur={(e) => onCustomNameChange(e.target.value)}
                                        options={customNameArr}
                                        renderInput={(params) => <TextField {...params} placeholder={langPack.form.selectOrInput} />}
                                    />
                                    {customKeyArr.length ? (
                                        <Autocomplete
                                            id="add-custom_key"
                                            freeSolo
                                            sx={{
                                                display: 'inline-flex',
                                                minWidth: '180px',
                                                padding: '0px',
                                                '& .MuiOutlinedInput-root': { height: '29px', padding: '1px' }
                                            }}
                                            onChange={(e, value) => onCustomKeyChange(value)}
                                            onBlur={(e) => onCustomKeyChange(e.target.value)}
                                            options={customKeyArr}
                                            renderInput={(params) => <TextField {...params} placeholder={langPack.form.selectOrInput} />}
                                        />
                                    ) : null}
                                    {customName ? (
                                        <Tooltip title={customTip} key="add-custom_value">
                                            <TextField
                                                inputProps={{ type: 'text' }}
                                                onChange={(e) => setCustomValue(e.target.value)}
                                                sx={{
                                                    display: 'inline-flex',
                                                    '& .MuiOutlinedInput-root': { height: '29px', padding: '1px' }
                                                }}
                                            />
                                        </Tooltip>
                                    ) : null}
                                    <Button startIcon={<CheckIcon />} onClick={onCustomAdd}>
                                        {langPack.common.yes}
                                    </Button>
                                    <Button startIcon={<CancelIcon />} onClick={() => setCustomDisplay((prev) => !prev)}>
                                        {langPack.common.no}
                                    </Button>
                                </>
                            )}
                            <Divider />
                            <br />
                            {finalCustomArr.map((obj) => (
                                <Tooltip title={langPack.table.min_order_quantity} key={obj.custom_type + obj.min_order_quantity}>
                                    <TextField
                                        margin="normal"
                                        id={obj.custom_type}
                                        key={obj.custom_type}
                                        inputProps={{ type: 'number' }}
                                        variant="outlined"
                                        sx={{ padding: '0px' }}
                                        label={
                                            <Typography variant="h3" sx={{ padding: '0px' }}>
                                                {obj.custom_type}
                                            </Typography>
                                        }
                                        defaultValue={obj.min_order_quantity}
                                        onBlur={(e) => {
                                            if (e.target.value.trim() === '') return queryResult(false, langPack.form.attributeMissing);
                                            onAttributeModified('custom_info', {
                                                custom_type: obj.custom_type,
                                                min_order_quantity: e.target.value
                                            });
                                        }}
                                    />
                                </Tooltip>
                            ))}
                        </>
                    </AccordionDetails>
                </Accordion>
            </div>
            <Divider />
            <br />
            <br />
            <br />
        </>
    );
}

ProductAttributes.propTypes = {
    langPack: PropTypes.object,
    expanded: PropTypes.object,
    setExpanded: PropTypes.func,
    sourcing_trade: PropTypes.object,
    subject: PropTypes.string,
    keywords: PropTypes.object,
    finalCustomArr: PropTypes.array,
    finalAttributeArr: PropTypes.array,
    allAttributeArr: PropTypes.array,
    onAttributeAdd: PropTypes.func,
    onAttributeModified: PropTypes.func,
    onAttributeClear: PropTypes.func
};

export default ProductAttributes;
